	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Glad it's over","date":"02 Aug 2000","slug":"/2000-08-02-glad-its-over/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/08/floor.jpg\" height=\"290\" width=\"342\" border=\"0\" alt=\"crh0208.jpg: \" /></p>\n<p>Yes, that's a hard tile kitchen floor that Christopher is rolling on. </p>\n<p>The <a href=\"http://www.abendblatt.de\">Hamburger Abendblatt</a> confirmed yesterday what I already knew. Last month had the worst weather for July in the past 50 years.</p>"},"next":{"fields":{"title":"Tag Team","date":"01 Aug 2000","slug":"/2000-08-01-tag-team/"}},"previous":{"fields":{"title":"Thursday","date":"03 Aug 2000","slug":"/2000-08-03-thursday/"}}},{"node":{"fields":{"title":"Tag Team","date":"01 Aug 2000","slug":"/2000-08-01-tag-team/","tags":"Y2000"},"html":"<p>We're experimenting. Mama is now a co-editor of this site, and we've changed the title accordingly. Now Mama can occaisionally post items without feeling guilty about being irregular.</p>"},"next":{"fields":{"title":"Without Glasses","date":"31 Jul 2000","slug":"/2000-07-31-without-glasses/"}},"previous":{"fields":{"title":"Glad it's over","date":"02 Aug 2000","slug":"/2000-08-02-glad-its-over/"}}},{"node":{"fields":{"title":"Without Glasses","date":"31 Jul 2000","slug":"/2000-07-31-without-glasses/","tags":"Y2000"},"html":"<p>Christopher is now 7 months old. Here's a quick list of his accomplishments to date.</p>\n<ul>\n<li>He's completely finished nursing. He's drinking HA (hypo-allergenic) formula mornings and evenings, a glass of vegetables at noon, and a glass of fruit in the afternoon. We just got a \"Baby's Cookbook\" with some nutrition hints.\n</li>\n<li>His diapers have increased in volume and pugnancy proportional to his intake of solid food. He likes to roll on the diaper table, not yet appreciating the drop to the floor.\n</li>\n<li>He's not crawling yet, but has increased his speed and mobility in rolling. He can roll across the living room in 2 seconds flat. It's definitely time to get guard rails for the stairs.\n</li>\n<li>He's become dangerously interested in cables and any loose objects within reach. He shakes things wildly in the air, and is surprised that it hurts when he hits himself in the head.\n</li>\n<li>He's sleeping long and soundly through the night, and naps for a couple of hours in the afternoon. Sometimes he naps in the morning too.\n</li>\n<li>He makes all sorts of different sounds, like he's testing the pitch and loudness of his voice. Sometimes it sounds like he's auditioning for the soundtrack of a horror film.\n</li>\n<li>We've taken him swimming twice, and his baths have become more active and wet (for parents and child) as a result.\n</li>\n<li>Mama thinks that he's already saying 'Mama', but to me it sounds like he's philosophizing about mammon or mums. Or he's learning the words to Surfing Bird.\n</li>\n</ul>"},"next":{"fields":{"title":"Lola","date":"29 Jul 2000","slug":"/2000-07-29-lola/"}},"previous":{"fields":{"title":"Tag Team","date":"01 Aug 2000","slug":"/2000-08-01-tag-team/"}}}],"pathPrefix":"","first":false,"last":false,"index":1049,"pageCount":1101}}

